import { useEffect, useRef, useState } from 'react';

export default function MainBanner() {
  const videoRefs = useRef<(HTMLVideoElement | null)[]>([]);
  const [currentVideo, setCurrentVideo] = useState(0);

  const videos = [
    {
      src: 'https://vgzol4tn11543.edge.naverncp.com/toxnfill/movies/main_pc_0922.mp4',
      poster: 'https://vgzol4tn11543.edge.naverncp.com/toxnfill/movies/banner_movie_poster.jpg',
    },
    {
      src: 'https://vgzol4tn11543.edge.naverncp.com/toxnfill/movies/main_pc01.mp4',
      poster: 'https://www.toxnfill.com/imges/brand/main-banner250313.jpg',
    },
    {
      src: 'https://vgzol4tn11543.edge.naverncp.com/toxnfill/movies/main_pc03.mp4?v=250616',
      poster: 'https://www.toxnfill.com/imges/brand/main-banner250313.jpg',
    },
  ];

  useEffect(() => {
    const currentVideoElement = videoRefs.current[currentVideo];
    if (currentVideoElement) {
      currentVideoElement.play();

      const handleEnded = () => {
        setCurrentVideo((prev) => (prev + 1) % videos.length);
      };

      currentVideoElement.addEventListener('ended', handleEnded);
      return () => {
        currentVideoElement.removeEventListener('ended', handleEnded);
      };
    }
  }, [currentVideo, videos.length]);

  return (
    <section className="relative w-full h-screen overflow-hidden">
      {videos.map((video, index) => (
        <video
          key={index}
          ref={(el) => {
            videoRefs.current[index] = el;
          }}
          className={`absolute inset-0 w-full h-full object-cover transition-opacity duration-1000 ${
            index === currentVideo ? 'opacity-100' : 'opacity-0'
          }`}
          muted
          playsInline
          poster={video.poster}
        >
          <source src={video.src} type="video/mp4" />
        </video>
      ))}
    </section>
  );
}
