          href="https://www.bbgnetworks.com/recruitment#franchise"
          target="_blank"
          rel="noopener noreferrer"
          className="flex items-center justify-center gap-3 max-w-md mx-auto px-8 py-4 bg-primary text-white rounded-full hover:bg-red-700 transition-all cursor-pointer"
